import { useState, useEffect, } from 'react';
import { useParams} from 'react-router-dom';
// @mui
import {
  Box,
  Grid,
  Card,
  Table,
  Divider,
  TableRow,
  Container,
  TableCell,
  Typography,
  TableContainer,
  Backdrop,
  CircularProgress
} from '@mui/material';
import Image from '../../components/Image';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
// utils
import { fCurrency } from '../../utils/formatNumber';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Label from '../../components/Label';
import Scrollbar from '../../components/Scrollbar';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// sections
import { InvoiceToolbar } from '../../sections/@dashboard/invioce/bill';
import axios from '../../utils/axios';
import useAuth from '../../hooks/useAuth';
// ----------------------------------------------------------------------

export default function BillInvoice() {
  const { themeStretch } = useSettings();
  const { name = '' } = useParams();
  const [trans, Settrans] = useState('');
  const [load,SetLoad] = useState(true);
  const {setting} = useAuth();

  useEffect(()  => {
    axios.get(`/api/bill/trans/${name}/secure`).then(data => {
           Settrans(data.data?.trans);
           SetLoad(false)
           }).catch(() => SetLoad(false));
    }, [name, Settrans]);  

  return (
    <Page title="Electric Sub: Invoice">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Electricity Subscription Invoice"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'Bill sub - Invoice' },
          ]}
        />

       {trans && <InvoiceToolbar invoice={trans} /> }

        <Card sx={{ pt: 5, px: 5 }}>
          <Grid container>
            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
            <Image disabledEffect visibleByDefault alt="logo" src="/upload/welcome.png" sx={{ maxWidth: 120 }} />
            </Grid>

            <Grid item xs={12} sm={6} sx={{ mb: 5 }}>
              <Box sx={{ textAlign: { sm: 'right' } }}>
                <Label color={trans?.plan_status === 1 ? 'success' : 'error'} sx={{ textTransform: 'uppercase', mb: 1 }}>
                  {trans?.plan_status === 1 && 'successful' || trans?.plan_status === 0 && 'proccessing' || trans?.plan_status === 2 && 'failed'}
                </Label>
                <Typography variant="h6">{trans?.transid}</Typography>
              </Box>
            </Grid>

          </Grid>

          <Scrollbar>
            <TableContainer>
            {trans && <Table>
                <TableRow>
                  <TableCell align="left"><Typography variant="h6">Disco Name</Typography></TableCell>
                  <TableCell>{trans?.disco_name}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Meter Type</Typography></TableCell>
                  <TableCell>{trans?.meter_type}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Meter Number</Typography></TableCell>
                  <TableCell>{trans?.meter_number}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Customer Name</Typography></TableCell>
                  <TableCell>{trans?.customer_name}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Amount</Typography></TableCell>
                  <TableCell>₦{fCurrency(trans?.amount)}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Charges</Typography></TableCell>
                  <TableCell>₦{fCurrency(trans?.charges)}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Old Balance</Typography></TableCell>
                  <TableCell>₦{fCurrency(trans?.oldbal)}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">New Balance</Typography></TableCell>
                  <TableCell>₦{fCurrency(trans?.newbal)}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">Purchased Code</Typography></TableCell>
                  <TableCell>{trans?.token}</TableCell>
                  </TableRow>
                  <TableRow>
                  <TableCell align="left"><Typography variant="h6">System</Typography></TableCell>
                  <TableCell>{trans?.system}</TableCell>
                  </TableRow>
                   <TableRow>
                  <TableCell align="left"><Typography variant="h6">Date</Typography></TableCell>
                  <TableCell>{trans?.plan_date}</TableCell>
                  </TableRow>
              </Table>}
            </TableContainer>
          </Scrollbar>

          <Divider sx={{ mt: 5 }} />

          <Grid container>
            <Grid item xs={12} md={9} sx={{ py: 3 }}>
              <Typography variant="subtitle2">NOTES</Typography>
              <Typography variant="body2">
              We appreciate your continuous patronage. Your satisfaction is our top priority!
              </Typography>
            </Grid>
            <Grid item xs={12} md={3} sx={{ py: 3, textAlign: 'right' }}>
              <Typography variant="subtitle2">any enquiry, kindly contact us @</Typography>
              <Typography variant="body2">{setting?.general?.app_email}</Typography>
            </Grid>
          </Grid>
        </Card>
        <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
      </Container>
    </Page>
  );
}
